.sort {
  white-space: nowrap;
  cursor: pointer;
  &::after {
    content: url('data:image/svg+xml;base64,');
    margin-left: 0.25rem;
  }
}
.sort.desc {
  &::after {
    content: url('data:image/svg+xml;base64,') !important;
    margin-top: -1px !important;
  }
}
.sort.asc {
  &::after {
    content: url('data:image/svg+xml;base64,') !important;
    margin-top: -1px !important;
  }
}

.pagination {
  li.active {
    .page {
      color: $primary !important;
    }
  }
}

.simplebar-content-wrapper::-webkit-scrollbar {
  display: none;
}
